@use 'abstracts/mixins' as *;

.jumbotron {
  padding-top: 166px;

  @include md {
    padding-top: 76px;
  }

  .section-title {
    color: var(--white);
    font-size: 20px;
    margin-bottom: 23px;
    padding-left: 0;
    margin-left: -1px;
    letter-spacing: 2px;

    &::before {
      font-size: 20px;
      margin-right: 4px;

      @include md {
        font-size: 16px;
      }
    }

    @include md {
      font-size: 16px;
      margin-bottom: 21px;
      letter-spacing: 1.6px;
    }
  }
}

.intro {
  padding-top: 60px;
  padding-bottom: 40px;

  @include md {
    padding-top: 46px;
  }

  .container {
    display: flex;
    align-items: flex-end;
    flex-direction: column;
  }

  .page-body {
    flex-shrink: 0;
    padding-right: 12px;
    padding-left: 3px;
    color: var(--dark-grey);
    margin-bottom: 28px;

    ul {
      padding-left: 20px;
      list-style: circle;
    }

    p,
    li {
      font-size: 20px;
      line-height: 28px;
      letter-spacing: 0.5px;
      font-weight: 300;
      margin-bottom: 8px;
      font-family: var(--unnamed-font-family-garamond-premier-pro);
    }

    a {
      color: var(--dark-grey);
    }

    h2 {
      font-size: 28px;
      line-height: 36px;
      letter-spacing: 0.7px;
      font-weight: 500;
      margin-top: 28px;
      margin-bottom: 16px;
    }

    h3 {
      font-size: 20px;
      line-height: 28px;
      letter-spacing: 0.5px;
      font-weight: 500;
      margin-top: 28px;
      color: var(--dark-grey);
    }

    @include md {
      font-size: 16px;
      line-height: 22px;
      letter-spacing: 0.4px;
      width: 100%;
      margin-right: 0;
      padding-right: 0;
      margin-bottom: 22px;
    }
  }
}

// COMMON

.esg__title {
  text-transform: uppercase;
  font-size: 28px;
  letter-spacing: 2.8px;
  color: var(--mid-green);
  display: flex;
  align-items: center;
  padding-left: 37px;

  @include md {
    font-size: 16px;
    letter-spacing: 1.6px;
    padding-left: 0;
  }

  &::before {
    content: '';
    display: block;
    background: url('./../assets/title-logo.svg');
    background-size: cover;
    margin-right: 16px;
    position: relative;
    top: 5px;
    height: 72px;
    width: 72px;
    flex-shrink: 0;

    @include md {
      content: '';
      width: 41px;
      margin-right: 9px;
      height: 41px;
      top: -1px;
    }
  }
}

.section-container {
  padding-left: 193px;
  max-width: 1000px;

  @include md {
    padding-left: 0;
  }
}

.esg-companies {
  display: flex;
  flex-wrap: wrap;
  margin-right: 179px;
  max-width: 640px;
  width: 70%;
  margin-top: 50px;
  margin-bottom: 60px;
  align-items: center;
  justify-content: center;

  div {
    margin: 0 15px 40px 15px;
  }

  @include md {
    width: 100%;
    margin-right: 0;
    padding-right: 0;
    margin-bottom: 60px;

    div {
      width: 100%;
      display: flex;
      justify-content: center;
      margin-bottom: 30px;
    }
  }
}

.esg-container {
  background-color: var(--white);
  background-image: url('./../assets/pricing-bg.webp');
  background-position: right -140px top 150px;
  background-size: 876px;
  background-repeat: no-repeat;

  @include md {
    background-size: 517px;
    background-position: right -270px top 270px;
  }
}
